import { payload } from '../dummydata';
import factory from './factory';

export class Core {
  constructor() {
    const preInfo = this.getPreflightInfo();
    const layers = this.checkForLayers(preInfo)

    if(layers.length > 0) {
      factory.renderComponents(layers);
    }
  }

  checkForLayers() {
    //check the server
    return payload;
  }

  getPreflightInfo() {
    return  {
      'href': window.location.href,
    }
  }
}

export default Core;


