export class Dialog {
  constructor(serverConfig) {
    this.config = serverConfig;
    this.appendTemplate();
  }

  getTemplate(nanoData) {
    return `<div class="lc-dialog lc-dialog--${nanoData.options.type}">
              <p>${nanoData.content.text}</p>
           </div>`
  }

  appendTemplate() {
    const handle = document.querySelector('body');
    const container = document.createElement('div');

    container.innerHTML = this.getTemplate(this.config)
    handle.appendChild(container);
  }
}

export default Dialog;


